body {
  background-color: rgb(250, 250, 250);
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.title {
  max-width: 4000px;
  margin-top: 50px !important;
}

@media (max-width: 770px) {
  .container {
    margin: 0 10%;
  }

  .title {
    width: 100%;
  }
}
